import fetch from 'isomorphic-fetch';
import hyperApp from 'hyperhtml-app'
import {hyper} from 'hyperhtml'
import * as qs from 'query-string'

const initialPath = '/' + location.search

var app = hyperApp();


app.get('/', async (ctx, next) => {
  const operationParameters = qs.parse(location.search)

  if (operationParameters.code) {
    const token = await getToken(operationParameters.code)
    // console.log('saving token', token)
    localStorage.setItem('token', token.data.Token);

    app.navigate(operationParameters.state)
  }

  if (operationParameters.op) {
    const operation = operationParameters.op
    delete operationParameters.op
    const operationResult = await runOperation(operation, operationParameters)
    console.log(operationResult)
  
    if (operationResult.errors && operationResult.errors.length > 0) {
      const error = operationResult.errors[0]
      if (error.name === 'AuthError') {
        const redirectToUrl = error.data.url
        document.documentElement.className = 'notok';
        hyper(document.getElementsByTagName('body')[0])`
          <h3>${error.name}</h3>
          <p>${error.message}</p>
        `;
        setTimeout(() => {
          app.navigate(redirectToUrl);
        }, 1000);
      } else {
        document.documentElement.className = 'notok';
        hyper(document.getElementsByTagName('body')[0])`
          <p>${error.message}</p>
        `;
      }
    } else {
      document.documentElement.className = 'ok';
      hyper(document.getElementsByTagName('body')[0])`
        <h3>${operation}</h3>
        <p>Result: ${operationResult.data[operation]}</p>
      `;      
      // setTimeout(() => {
      //   window.open('','_parent','');
      //   window.close();
      // }, 3000);
    }
    next()  
  }
});

