app.navigate(initialPath);


function runOperation(operation, params) {
  let operationParams = ''
  if (Object.keys(params).length > 0) {
    operationParams = `( ${Object.keys(params).map(key => `${key}: "${params[key]}"`).join(" ")} )`
  }

  // console.log(`{ ${operation}( ${Object.keys(params).map(key => `${key}: "${params[key]}`).join(" ")} ) }`)
  return requestOperation(`{ ${operation} ${operationParams} }`, null);
}

function getToken(code) {
  return requestOperation(`{ Token(code:"${code}") }`, null)
}

function requestOperation(query, variables) {
  const token = localStorage.getItem('token')
  console.log(token)
  const options = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      'authorization': localStorage.getItem('token')
    },
		body: JSON.stringify({
			query: query,
			variables: variables
		})
	};

	return fetch("http://localhost:8010", options)
		.then(function(res) {
			return res.json();
		}).then(function(json) {
			// console.log('got token data', json);

			return json;
		}).catch((err) => {
			console.log('data error', err);
		});

}
